import { bus } from '../../utils'
import InformationModelUpload from '../InformationModelUpload/InformationModelUpload.vue'

export default {
  components: { InformationModelUpload },
  created () {
    bus.$on('updateModelList', async(models) => {
      this.updateModelList(models)
    })
  },
  data () {
    return {
      alert$: {
        class: '',
        show: false,
        text: ''
      },
      informationModels: [],
      loading: false
    }
  },
  methods: {
    async getInformationModels () {
      let informationModels = await this.$api.getInfoModels()
        .then(r => r.data)
        .catch(e => [])

      this.updateModelList(informationModels)

      return informationModels
    },
    async removeInformationModel (familyName) {
      this.loading = true
      this.alert('info', 'Restoring...', 0)
      await this.$api.removeInfoModel({
        familyName
      })
        .then(r => r.data)
        .then(this.updateModelList.bind(this))
        .then(this.successCallback.bind(this))
        .catch(this.failedCallback.bind(this))
    },
    alert (style, message, timeout = 2000) {
      this.alert$.class = 'alert-' + style
      this.alert$.text = message
      this.alert$.show = true

      if (style === 'danger') return

      clearTimeout(this.alert._timeout)
      if (timeout) {
        this.alert._timeout = setTimeout(
          () => (this.alert$.show = false),
          timeout
        )
      }
    },
    failedCallback (e) {
      this.loading = false
      this.alert('danger', e.body)
    },
    addInformationModal () {
      bus.$emit('add-information-modal', true)
    },
    successCallback () {
      this.loading = false
      this.alert('success', 'Success')
    },
    updateModelList (models) {
      this.informationModels = models
    }
  },
  async mounted () {
    return this.getInformationModels()
  }
}



