import timeLine from '../Timeline/Timeline.vue'

export default {
  name: 'UserInfo',
  components: {
    timeLine
  },
  computed: {
    alexaStatus () {
      if (this.raw.alexa_status === 0) return 'Not connected'
      if (this.raw.alexa_status === 1) return 'Connected'
      return '--'
    },
    devices () {
      return this.raw.devices || []
    },
    groups () {
      return this.raw.groups || []
    }
  },
  data () {
    return {
      raw: {}
    }
  },
  props: {
    isClicked: {
      type: Boolean
    },
    user: {
      type: Object
    }
  },
  watch: {
    async isClicked (newStatus) {
      if (!newStatus) return
      this.raw = await this.$api.getUser({id: this.user.id})
        .then(res => res.data)
    }
  }
}



