import config from '../../config'
import store from '../../store/index'
import Sidebar from '../Sidebar/Sidebar.vue'

export default {
  name: 'Dash',
  components: {
    Sidebar
  },
  computed: {
    username () {
      return store.state.user
    }
  },
  data: function () {
    return {
      year: new Date().getFullYear(),
      classes: {
        fixed_layout: config.fixedLayout,
        hide_logo: config.hideLogoOnMobile
      },
      footer: {
        exosite: {
          divClass: 'col-md-8 no-padding',
          href: 'https://exosite.com',
          target: '_blank',
          text: 'Exosite'
        },
        privacy: {
          divClass: 'col-md-1 no-padding',
          href: 'https://exosite.com/privacy-policy/',
          text: 'Privacy'
        },
        support: {
          divClass: 'col-md-1 no-padding',
          href: 'https://support.exosite.com/hc/en-us/categories/115001106563-Home-Automation-Micro-Vertical-Engine',
          text: 'Support'
        },
        terms: {
          divClass: 'col-md-1 no-padding',
          href: 'https://exosite.com/murano-terms-conditions/',
          text: 'Terms'
        },
        version: {
          divClass: 'col-md-1 no-padding',
          text: 'version 2.1.0'
        }
      }
    }
  },
  methods: {
    onSignOut () {
      this.$store.commit('SET_TOKEN', null)
      this.$store.commit('SET_USER', '')
      this.$cookie.delete('sid')
      window.localStorage.clear()
      this.$router.go('/login')
    }
  }

}



