import moment from 'moment'

moment.updateLocale('en', {
  relativeTime: {
    M: '1 month',
    MM: '%d months',
    d: '1 day',
    dd: '%d days',
    future: 'in %s',
    h: '1 hour',
    hh: '%d hours',
    m: '1 min',
    mm: '%d mins',
    past: '%s ago',
    s: (v) => {
      let unit = 'secs'
      if (v === 1) {
        unit = 'sec'
      }
      return `${v} ${unit}`
    },
    y: '1 year',
    yy: '%d years'
  }
})

export function durations (date) {
  // return +new Date() - date > 2678400000
          // ? moment(date).format('MM/DD/YYYY, HH:mm:ss')
  return moment(date).fromNow()
}

export function isConnective (status) {
  return status ? 'Yes' : 'No'
}

export function isProvision (status) {
  return status ? 'Online' : 'Offline'
}

export function formatDate (date) {
  return `${moment(date).format('MM/DD/YYYY, HH:mm:ss')}
          (${moment(date).fromNow()})`
}

export function pluralize (time, label) {
  return time === 1 ? `${time} ${label}` : `${time} ${label}s`
}



