import bus from '../../utils/bus'
import TimeLine from '../Timeline/Timeline.vue'

export default {
  components: {
    TimeLine
  },
  computed: {
    collapseStatus () {
      return this.isToggle
    },
    getConnectedStatus () {
      return this.connected ? 'Online' : 'Offline'
    },
    getConnectedStatusIcon () {
      return this.connected ? 'fa fa-circle device-online-icon' : 'fa fa-circle device-offline-icon'
    },
    getFirmwareVersion () {
      return this.firmwareVersion || '--'
    },
    index () {
      return 0
    },
    isLocked () {
      return this.locked
    },
    getModel () {
      return this.model || '--'
    },
    id () {
      return this.identity
    }
  },
  data () {
    return {
      isCollapsed: false,
      isToggle: false
    }
  },
  methods: {
    collapsed () {
      this.isToggle = !this.isToggle
      this.isCollapsed = true
    },
    comfirmLock (id) {
      bus.$emit('dialog-show', {
        title: 'Delete Device',
        text: `Are you sure you want to delete <small class="label label-default">${id}</small>?`,
        buttons: [
          { text: 'Close' },
          {
            text: 'Delete',
            click: () => {
              bus.$emit('dialog-hide')
              this.lockDevice(id)
            }
          }
        ]
      })
    },
    lockDevice (id) {
      this.$api.lockDevice(id)
        .then(() => {
          bus.$emit('DeviceLockPopup-lockDevice', id)
        }
      )
    }
  },
  props: {
    connected: {
      type: Boolean
    },
    locked: Boolean,
    firmwareVersion: {
      type: String
    },
    model: {
      type: String
    },
    identity: {
      type: String
    },
    owner: {
      type: String
    },
    guests: {
      type: Array
    }
  }
}



