<template>
  <div class="modal" tabindex="-1" role="dialog" v-if="showModal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" @click="hide">
            <span aria-hidden="true">&times;</span>
          </button>
          <h3 class="modal-title" id="exampleModalLabel">{{headline}}</h3>
        </div>
        <div class="modal-body">
          <div v-html="body"></div>
        </div>
        <div class="modal-footer">
          <button
            :class="button.class"
            :key="index"
            @click.stop="button.click"
            v-for="(button, index) in options.buttons"
          >{{button.text}}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import main from './Dialog.js'
export default main
</script>

<style lang="scss" scoped>
@import './Dialog.scss'
</style>



