import {
  AlexaLogin,
  Legal
} from './assets/pages'

import {
  Dash,
  Device,
  Firmware,
  InformationModel,
  Login,
  NotFound,
  Theme,
  User
} from './components'

// Routes
const routes = [
  {
    path: '/login',
    component: Login
  },
  {
    path: '/alexa-login',
    component: AlexaLogin
  },
  {
    path: '/legal',
    component: Legal
  },
  {
    path: '/',
    component: Dash,
    children: [
      {
        path: 'device',
        alias: '',
        component: Device,
        name: 'Device List',
        meta: {description: ''}
      },
      {
        path: 'device/:sn',
        component: Device,
        name: 'Device List',
        meta: {description: ''}
      },
      {
        path: 'firmware',
        component: Firmware,
        name: 'Firmware List',
        meta: {description: ''}
      },
      {
        path: 'theme',
        component: Theme,
        name: 'Theme',
        meta: {description: ''}
      },
      {
        path: 'theme/:id',
        component: Theme,
        name: 'Theme',
        meta: {description: ''}
      },
      {
        path: 'informationModel',
        component: InformationModel,
        name: 'Information Model',
        meta: {description: ''}
      },
      {
        path: 'user',
        component: User,
        name: 'User List',
        meta: {description: ''}
      },
      {
        path: 'user/:email',
        component: User,
        name: 'User List',
        meta: {description: ''}
      }
    ]
  },
  {
    // not found handler
    path: '*',
    component: NotFound
  }
]

export default routes



