<template>
  <div class="time-line-body">
    <div class="time-line-row" v-for="(item, index) of logList" :key="index" v-show="filter(item.type)">
      <div class="time-line-row-content">
        <div class="col-md-1 item-type">
          <span :class="{
                'tag-error':isError(item.type),
                'tag-event':!isError(item.type),
          }">
            {{item.type}}
          </span>
        </div>
        <div class="col-md-5">
          <div class="col-sm-12">
            <strong>{{item.action.label}}:</strong>
            {{item.action.content}}
            <span v-if="item.action.key">({{item.action.key}}: {{item.action.value}})</span>
            <a :href="navigate(item)" @click="clickId(item)">
              <ins>{{fromWhom(item)}}</ins>
            </a>
          </div>
        </div>
        <div class="col-md-2 view-info">
          <a @mouseover="getLocation(index, item.status, item.ip)"><ins v-tooltip="status[index] || item.status">View Info</ins></a>
        </div>
        <div class="col-md-3">
          {{item.time | formatDate}}
        </div>
        <div class="col-md-1" v-show="false">
          <button class="btn btn-danger">
            <i class="fa fa-flag"></i>
          </button>
        </div>
      </div>
    </div>
    <div class="empty-message" v-if="isEmpty">
      {{emptyMessage}}
    </div>
    <div class="empty-message" v-if="!logList">
      {{loadingMessage}}
    </div>
  </div>
</template>

<script>
import main from './TimelineLog.js'
export default main
</script>

<style lang="scss" scoped>
@import './TimelineLog.scss'
</style>

<style lang="scss">
@import './vTooltip.scss';
</style>



