<template>
  <div class="modal" id="add-information-model" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" v-show="showModal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" @click="close" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
          <h3 class="modal-title" id="exampleModalLabel">Add New Information Models</h3>
        </div>
        <form class="form-horizontal" @submit.prevent="submit">
          <div class="box-body">
            <div class="form-group" v-for="(item, id) in file" :key="id">
              <label :for="item.id" class="col-sm-4 control-label">
                {{item.label}}
              </label>
              <div class="col-sm-8">
                <input
                  :class="item.class"
                  :id="id"
                  multiple
                  required
                  type="file"
                >
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button v-for="item in buttons"
              :class="item.class"
              :disabled="loading || errors.any()"
              :key="`button-${item.text}`"
              :type="item.type"
              @click="item.onClick"
            >{{item.text}}</button>
          </div>
        </form>
        <div class="row" v-show="alert$.show">
          <div class="col-xs-12">
            <div
              :class="alert$.class"
              class="alert"
              role="alert"
            >{{alert$.text}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import main from './InformationModelUpload.js'
export default main
</script>

<style lang="scss">
@import './InformationModelUpload.scss'
</style>



