<template>
  <div id="accordion" class="panel box box-header-row">
    <div @click="collapsed">
      <div :class="['box-header', { clicked: collapseStatus } ]">
        <div class="collapsed" data-toggle="collapse" data-parent="#accordion" aria-expanded="false" aria-controls="collapseTwo">
          <div class="col-sm-5 box-title">{{id}}</div>
          <div class="col-sm-2 box-title">{{getModel}}</div>
          <div class="col-sm-2 box-title">
            <i v-if="getConnectedStatus !== '--'" :class="getConnectedStatusIcon"></i> {{getConnectedStatus}}</div>
          <div class="col-sm-2 box-title">{{getFirmwareVersion}}</div>
          <div class="col-sm-1 box-title">
            <a v-if="!isLocked" @click.stop="comfirmLock(id)">
              <i class="fa fa-trash fa-2"></i>
            </a>
          </div>
        </div>
      </div>
    </div>
    <transition :id="id" role="tabpanel" aria-labelledby="headingTwo">
      <div class="box-body panel-collapse" v-show="isToggle">
        <time-line
          :connected="getConnectedStatus"
          :is-clicked="isCollapsed"
          :target="id"
        ></time-line>
        <div class="col-sm-12">
          <div class="box box-gray">
            <div class="box-header with-border">
              <h3 class="box-title">Users</h3>
            </div>
            <div class="box-body">
              <dl class="dl-horizontal">
                <div class="col-sm-6">
                  <dt>OWNER</dt>
                  <dd>{{ owner }}</dd>
                </div>
                <div class="col-sm-6">
                  <dt>GUEST</dt>
                  <dd v-for="(guest, index) in guests" :key="index">{{ guest }}</dd>
                </div>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import main from './DeviceListItem.js'
export default main
</script>
<style lang="scss" scoped>
@import './DeviceListItem.scss'
</style>



