export default {
  name: 'Tabs',
  data () {
    return { tabs: [] }
  },

  created () {
    this.tabs = this.$children
  },

  mounted () {
    const routeTabId = this.$route.params.id
    this.setActiveTab(routeTabId)
  },

  watch: {
    '$route.params.id': function (id) {
      const routeTabId = this.$route.params.id
      this.setActiveTab(routeTabId)
    }
  },

  methods: {
    selectTab (selectedTab) {
      this.setActiveTab(selectedTab.id)
    },

    setActiveTab (id) {
      if (id) {
        this.tabs.forEach(tab => {
          tab.isActive = (tab.id === id)
        })
      }
    }
  }
}



