import _ from 'lodash'
import { bus } from '../../utils'

export default {
  name: 'Timeline',
  computed: {
    popupId () {
      let tempArray = _.split(this.target, /\W/)
      let targetString
      _.forEach(tempArray, item => (targetString += item))
      return targetString
    }
  },
  data () {
    return {
      lists: null,
      status: {},
      title: 'Timeline'
    }
  },
  methods: {
    async viewLogInfo (status) {
      let data = {
        content: ''
      }
      _.forEach(
        status,
        (value, key) =>
          (data.content += `<div class="col-md-4 text-left">${key}: ${value}</div>`)
      )
      return data
    },
    openPopup () {
      bus.$emit('TimelineModal-show', {
        connected: this.connected,
        target: this.target
      })
    },
    tagsFormat (target) {
      let format = _.includes(target, '@')
        ? { email: target, log_type: 'event' }
        : { device_unique_id: target }
      return JSON.stringify(format)
    }
  },
  props: {
    connected: {
      type: String
    },
    isClicked: {
      type: Boolean
    },
    target: {
      type: [String, Number],
      required: true
    }
  },
  watch: {
    async isClicked (newStates) {
      if (!newStates) return
      let logs = await this.$api.getLogs({
        limit: 5,
        tags: this.tagsFormat(this.target)
      })
        .then(res => res.data)
        .catch(e => console.log(e))

      if (!this.lists) {
        this.lists = []
      }
      _.forEach(logs, async item => {
        this.lists.push({
          action: item.action,
          from: item.from,
          ip: item.ip,
          type: _.toUpper(item.type),
          status: await this.viewLogInfo(item.status, item.ip),
          time: item.time
        })
      })
    }
  }
}



