import _ from 'lodash'

export default {
  name: 'DeviceInfoBox',
  computed: {
    numberOfConnection () {
      return this.total.connected_devices || this.total.connected_devices === 0 ? this.total.connected_devices : '--'
    },
    numberOfProvision () {
      return this.total.devices || this.total.devices === 0 ? this.numberOfFormat(this.total.devices, this.total.devices) : '--'
    },
    rateOfProvision () {
      return this.rate(this.total.devices, this.total.devices)
    },
    numberOfDevices () {
      return this.total.devices || this.total.devices === 0 ? this.total.devices : '--'
    }
  },
  methods: {
    numberOfFormat (fractions, numerator) {
      return `${fractions} / ${numerator}`
    },
    rate (fractions, numerator) {
      return _.ceil(_.divide(fractions, numerator) * 100, 2)
    }
  },
  props: {
    total: {
      required: true,
      type: Object
    }
  }
}



