export default {
  name: 'Tab',
  props: {
    id: { required: true },
    name: { required: true },
    selected: { default: false }
  },

  data () {
    return {
      isActive: false
    }
  },

  computed: {
    href () {
      const mainPath = this.$route.path.split('/')[1]
      return `#${mainPath}/${this.id}`
    }
  },

  mounted () {
    this.isActive = this.selected
  }
}



