import { bus } from '../../utils'

export default {
  name: 'DeletePopUp',
  data () {
    return {
      data: {
        formData: null,
        imgFile: null,
        model: null,
        lastVersion: null,
        sha1: null,
        version: null
      },
      percentCompleted: '0%',
      showModal: false,
      uploadedFiles: [],
      uploadError: null,
      uploadFieldName: 'photos',
      validate: {
        model: /^([.A-Za-z0-9_-]){2,32}$/,
        semver: /[0-9]+\.[0-9]+\.[0-9]+/,
        sha1: /^([0-9a-f]){40}$/
      }
    }
  },
  methods: {
    close () {
      this.reset()
      this.showModal = false
    },
    filesChange (fieldName, fileList) {
      this.data.formData = new FormData()

      this.data.formData.append('inputVersion', this.data.version)
      this.data.formData.append('inputLastVersion', this.data.lastVersion)
      this.data.formData.append('inputSHA1', this.data.sha1)
      this.data.formData.append('inputModel', this.data.model)
      if (!fileList.length) return

      // append the files to FormData
      Array
        .from(Array(fileList.length).keys())
        .map(x => {
          this.data.formData.append(fieldName, fileList[x], fileList[x].name)
        })
    },
    reset () {
      this.data = {
        formData: null,
        imgFile: null,
        model: null,
        lastVersion: null,
        sha1: null,
        version: null
      }
      this.percentCompleted = '0%'
      this.uploadedFiles = []
      this.uploadError = null
      document.getElementById('file').value = ''
    },
    uploadFirmware () {
      this.$validator.validateAll()
        .then(async (result) => {
          if (result) {
            const config = {
              onUploadProgress: (progressEvent) => {
                const percentCompleted = Math.floor((progressEvent.loaded * 100) / progressEvent.total)
                this.percentCompleted = `${percentCompleted}%`
              }
            }
            try {
              const upload = await this.$api.addFirmware(this.data.formData, config)
              if (upload.status !== 200) {
                return
              }
              bus.$emit('getFirmwareList')
              this.close()
            } catch (error) {
              console.log(error)
            }
          }
        })
    }
  },
  mounted () {
    bus.$on('show-add-firmware', (show) => {
      if (show) {
        this.showModal = true
      }
    })
  }
}



