<template>
  <section class="content"
    infinite-scroll-disabled="infiniteScrollDisabled"
    infinite-scroll-distance="20"
    infinite-scroll-immediate-check="false"
    v-infinite-scroll="infiniteScrollCallback"
  >
    <device-infoBox :total="total"></device-infoBox>
    <div class="row center-block">
      <div class="col-sm-3 col-sm-offset-9">
        <div class="input-group search">
          <span class="input-group-addon">
            <i class="fa fa-search"></i>
          </span>
          <input
            @change="deviceSearch(searchQuery)"
            class="form-control input-sm"
            placeholder="Search SN"
            type="search"
            v-model.lazy="searchQuery"
          >
        </div>
      </div>
      <div class="col-md-12">
        <div class="box table-list">
          <div class="box-body">
            <div class="form-inline" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-12">
                  <div class="panel box title">
                    <div class="box-header" >
                      <div :class="item.class" v-for="(item, id) in tableHeader" :key="id"><strong>{{item.label}}</strong></div>
                    </div>
                  </div>
                </div>
                <div class="col-sm-12 no-devices" v-if="!deviceList.length && !isLoading">
                  <p>No devices found.</p>
                  <p>If you need help getting started or have any questions, contact our support at <a href="mailto:support@exosite.com">support@exosite.com</a></p>
                </div>
                <div class="col-sm-12" v-else>
                  <device-listItem
                    :connected="device.connected"
                    :locked="device.locked"
                    :firmware-version="device.firmware_version"
                    :guests="device.guest_emails"
                    :identity="device.identity"
                    :key="device.identity"
                    :model="device.model"
                    :owner="device.owner_email"
                    v-for="device in deviceList"
                  ></device-listItem>
                  <div id="loading-icon" v-show="isLoading">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                    <span class="sr-only">Loading...</span>
                  </div>
                  <div class="col-sm-12 text-center" v-show="hasResult">
                    <h4>{{searchNotFound}}</h4>
                  </div>
                  <div class="col-sm-12 footer" v-if="deviceList.length > 0">
                    <div class="col-lg-12 load-more" v-show="isLoadMore">
                      <button class="btn btn-block btn-primary" @click="button.loadMore.onClick()">{{button.loadMore.text}}</button>
                    </div>
                    <div class="col-lg-12 need-search-tips" v-show="!isLoading && !hasResult && total.devices > 50">{{needSearchTips}}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
import main from './Device.js'
export default main
</script>
<style lang="scss" scoped>
@import './Device.scss'
</style>



