<template>
  <div :class="['wrapper', classes]">
    <header class="main-header">
      <a href="/#" class="logo">
        <!-- mini logo for sidebar mini 40x50 pixels -->
        <span class="logo-mini"><img src="/src/assets/img/logo-navbar.png" alt="Logo" class="img-responsive center-block"></span>
        <!-- logo for regular state and mobile devices -->
        <div class="logo-lg">
          <img src="/src/assets/img/logo-navbar.png" alt="Logo" class="img-responsive">
          <span>Home Automation MV</span>
        </div>
      </a>

      <!-- Header Navbar -->
      <nav class="navbar navbar-static-top" role="navigation">
        <!-- Sidebar toggle button-->
        <a href="javascript:;" class="sidebar-toggle" data-toggle="offcanvas" role="button">
          <span class="sr-only">Toggle navigation</span>
        </a>
        <!-- Navbar Right Menu -->
        <div class="navbar-custom-menu">
          <ul class="nav navbar-nav">
            <!-- User Account Menu -->
            <li class="dropdown user user-menu">
              <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown">
                <span class="hidden-xs">{{username}}</span>
                <span class="caret"></span>
              </a>
              <ul class="dropdown-menu">
                <li>
                  <a @click="onSignOut" href="javascript:;">
                    <h4>Sign Out</h4>
                  </a>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </nav>
    </header>
    <!-- Left side column. contains the logo and sidebar -->
    <sidebar />

    <!-- Content Wrapper. Contains page content -->
    <div class="content-wrapper">
      <!-- Content Header (Page header) -->
      <section class="content-header">
        <h1>
          {{$route.name.toUpperCase() }}
          <small>{{ $route.meta.description }}</small>
        </h1>
      </section>

      <router-view></router-view>
    </div>
    <!-- /.content-wrapper -->

    <!-- Main Footer -->
    <footer class="main-footer row">
      <div :class="footer.exosite.divClass">
        &copy; {{year}}
        <a :href="footer.exosite.href" :target="footer.exosite.target">{{footer.exosite.text}}</a>.
        All rights reserved.
      </div>
      <div :class="footer.support.divClass">
        <a :href="footer.support.href">{{footer.support.text}}</a>
      </div>
      <div :class="footer.terms.divClass">
        <a :href="footer.terms.href">{{footer.terms.text}}</a>
      </div>
      <div :class="footer.privacy.divClass">
        <a :href="footer.privacy.href">{{footer.privacy.text}}</a>
      </div>
      <div :class="footer.version.divClass">
        <span>{{footer.version.text}}</span>
      </div>
    </footer>
  </div>
  <!-- ./wrapper -->
</template>

<script>
import main from './Dash.js'
export default main
</script>

<style lang="scss" scoped>
@import './Dash.scss'
</style>



