import VueResource from 'vue-resource'
import config from '../config'

const MuranoApiPlugin = {}
MuranoApiPlugin.install = function (Vue, options) {
  Vue.use(VueResource)
  if (localStorage.token) {
    updateAuthHeader(Vue, localStorage.token)
  }

  const vue = new Vue()
  const admin = createResource(vue, '/api:1/admin')
  const device = createResource(vue, '/api:2/devices{/id}')
  const devices = createResource(vue, '/api:2/devices')
  const firmwareDelete = createResource(vue, '/api:1/fw/delete', {}, {}, {
    headers: {
      'content-type': 'application/x-www-form-urlencoded'
    }
  })
  const firmwareList = createResource(vue, '/api:1/fw/list')
  const firmwareUpload = createResource(vue, '/api:1/fw/upload')
  const im = createResource(vue, '/api:1/im')
  const infoModel = createResource(vue, '/api:1/info-model')
  const legal = createResource(vue, '/api:1/term_of_service')
  const log = createResource(vue, '/api:1/log')
  const session = createResource(vue, '/api:1/session')
  const theme = createResource(vue, '/api:1/theme')
  const total = createResource(vue, '/api:1/total')
  const users = createResource(vue, '/api:1/users')
  const user = createResource(vue, '/api:1/users{/id}')

  Vue.prototype.$api = {
    addFirmware (data) {
      return firmwareUpload.save({}, data)
    },
    checkToken (params) {
      return admin.get()
    },
    getGeoip (ip) {
      if (!ip || ip === 'none') {
        return Promise.resolve({
          country_name: '--',
          region_name: '--'
        })
      }

      return fetch(`https://freegeoip.net/json/${ip}`)
        .then(res => res.json())
    },
    getDevices (query) {
      return devices.get(query)
    },
    getIms () {
      return im.get()
    },
    getInfoModels () {
      return infoModel.get()
    },
    getLegal () {
      return legal.get()
    },
    getLogs (query) {
      return log.get(query)
    },
    getFirmwareList () {
      return firmwareList.get()
    },
    getSession () {
      return session.get()
    },
    getTheme () {
      return theme.get()
    },
    getTotal (query) {
      return total.get(query)
    },
    getUsers (query) {
      return users.get(query)
    },
    getUser (id) {
      return user.get(id)
    },
    login (params) {
      return session.save({}, params)
        .then(res => {
          const token = res.data.token
          updateAuthHeader(Vue, token)
          return token
        })
    },
    lockDevice (id) {
      return device.delete({ id })
    },
    removeFirmware (data) {
      return firmwareDelete.save({}, data)
    },
    removeIm (data) {
      return im.delete({}, data)
    },
    removeInfoModel (data) {
      return infoModel.delete({}, data)
    },
    removeUser (id) {
      return user.delete({
        id
      })
    },
    updateIm (data) {
      return im.update(data)
    },
    updateInfoModel (data) {
      return infoModel.update(data)
    },
    updateLegal (data) {
      return legal.update(data)
    },
    updateTheme (form) {
      return theme.save(form)
    }
  }
}

function updateAuthHeader (Vue, token) {
  Vue.http.headers.common['Authorization'] = 'Bearer ' + token
}

function createResource (Vue, url, ...args) {
  return Vue.$resource(config.serverURI + url, ...args)
}

export default MuranoApiPlugin



