<template>
  <video-bg :sources="['/src/assets/video/video-bg.mp4']" img="/src/assets/img/video-bg.jpg">
    <div class="container container-table">
      <div class="row vertical-10p over-particles">
        <div class="container">
          <img src="/src/assets/img/admin-logo.png" class="center-block logo">
          <div class="text-center col-md-4 col-sm-offset-4">
            <!-- login form -->
            <form class="ui form loginForm" @submit.prevent="checkCreds">

              <div class="input-group">
                <span class="input-group-addon">
                  <i class="fa fa-envelope-open email"></i>
                </span>
                <input class="form-control" name="email" placeholder="Email" type="text" v-model="email">
              </div>

              <div class="input-group">
                <span class="input-group-addon">
                  <i class="fa fa-unlock-alt password"></i>
                </span>
                <input class="form-control" name="password" placeholder="Password" type="password" v-model="password">
              </div>
              <button type="submit" v-bind:class="'btn btn-primary btn-lg btn-block ' + loading">Sign In</button>
            </form>

            <!-- errors -->
            <div v-if=response class="box box-solid box-danger text-black" style="margin-top: 20px;">
              <div class="box-header">
                <h3 class="box-title">Error</h3>
              </div>
              <div class="box-body">
                {{response}}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </video-bg>
</template>

<script>
import main from './Login.js'
export default main
</script>

<style lang="scss">
@import './Login.scss'
</style>



