<template>
  <section class="content page-information-model">
    <button type="button" id="btn-add-information-model" @click="addInformationModal" class="btn btn-primary">Add Information Model</button>
    <im-upload></im-upload>
    <div class="row">
      <div class="col-md-12 col-lg-12">
        <div class="box box-primary">
          <div class="box-header">
            <h3 class="box-title">Models List</h3>
          </div>
          <div class="box-body table-responsive no-padding">
            <table class="table table-hover">
              <tbody>
                <tr>
                  <th>Family Name</th>
                  <th>File Name</th>
                  <th>Last Modified</th>
                  <th>Action</th>
                </tr>
                <tr v-for="(item, familyName) in informationModels">
                  <td>{{familyName}}</td>
                  <td>{{item.name}}</td>
                  <td>{{item.lastModified | formatDate}}</td>
                  <td>
                    <a :href="'/api:1/info-model/' + familyName" >
                      <i class="fa fa-download fa-2"></i>
                    </a>
                    <a @click="removeInformationModel(familyName)">
                      <i class="fa fa-trash fa-2"></i>
                    </a>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div class="row" v-show="alert$.show">
      <div class="col-xs-12">
        <div
          :class="alert$.class"
          class="alert"
          role="alert"
        >{{alert$.text}}</div>
      </div>
    </div>
  </section>
</template>
<script>
import main from './InformationModel.js'
export default main
</script>
<style lang="scss">
@import './InformationModel.scss'
</style>



