import _ from 'lodash'

import { bus } from '../../utils'
import FirmwareUpload from '../FirmwareUpload/FirmwareUpload.vue'

export default {
  name: 'Tables',
  created () {
    bus.$on('getFirmwareList', async() => {
      this.getFirmwareList()
    })
  },
  components: { FirmwareUpload },
  data () {
    return {
      active: 0,
      FirmwareList: {},
      popData: {},
      showModal: false
    }
  },
  mounted () {
    this.getFirmwareList()
  },
  methods: {
    delFirmware (info) {
      const { name: id, version } = info
      bus.$emit('dialog-show', {
        title: 'Delete Firmware',
        text: `
          Are you sure you want to delete
          <small class="label label-default">${version}</small>?
        `,
        buttons: [
          {
            text: 'Close'
          },
          {
            text: 'Delete',
            click: () => {
              this.deleteFirmware(id)
              bus.$emit('dialog-hide')
            }
          }
        ]
      })
    },
    showAddFirmware () {
      bus.$emit('show-add-firmware', true)
    },
    async deleteFirmware (id) {
      const data = `id=${id}`
      try {
        await this.$api.removeFirmware(data)
          .then(() => this.getFirmwareList())
      } catch (error) {
        console.log(error)
      }
    },
    async getFirmwareList () {
      const list = await this.$api.getFirmwareList()
      list.data = _.each(list.data, (value, index) => {
        value.index = index
      })
      this.FirmwareList = _.orderBy(list.data, ['version'], ['desc'])
    }
  }
}



