import _ from 'lodash'
import { bus } from '../../utils'

export default {
  name: 'TimelineLog',
  computed: {
    isEmpty () {
      return Array.isArray(this.logList) && _.isEmpty(this.logList)
    }
  },
  data () {
    return {
      emptyMessage: 'No logs found.',
      hasLocation: {},
      loadingMessage: 'Loading...',
      status: {}
    }
  },
  methods: {
    async getLocation (index, status, ip) {
      if (_.isUndefined(this.hasLocation[index])) {
        const location = await this.$api.getGeoip(ip)
        this.hasLocation[index] = {
          regionName: location.region_name,
          countryName: location.country_name
        }
      }

      const regionName = this.hasLocation[index].regionName
      const countryName = this.hasLocation[index].countryName

      this.status = Object.assign({}, this.status, {
        [index]: {
          content: `<div class="col-md-12 text-left"><strong>Location: ${regionName}, ${countryName}(${ip})</strong></div>${status.content}`
        }
      })
    },
    clickId () {
      bus.$emit('TimelineModal-hide')
    },
    filter (type) {
      if (_.isUndefined(this.filterRule)) return true

      let targetType = _.toLower(type)
      switch (targetType) {
        case 'error':
          return this.filterRule[0].model
        case 'event':
          return this.filterRule[1].model
        default:
          break
      }
    },
    fromWhom ({ action }) {
      let { path } = this.$route
      if (path.includes('/device')) {
        return action.email
      }
      if (path.includes('/user')) {
        return action.device_unique_id || action.sn
      }
      return ''
    },
    isError (type) {
      return _.toLower(type) === 'error'
    },
    navigate ({ action }) {
      let { path } = this.$route
      if (path.includes('/device')) {
        return `#user/${action.email}`
      }
      if (path.includes('/user')) {
        const id = action.device_unique_id || action.sn
        return `#device/${id}`
      }
      return ''
    }
  },
  props: {
    connected: {
      type: String
    },
    filterRule: {
      type: Array
    },
    logList: {
      required: true
    }
  }
}



