import VueColor from 'vue-color'

import bus from '../../utils/bus'

export default {
  name: 'ColorPicker',
  components: {
    'chrome-picker': VueColor.Chrome
  },
  props: [
    'classes',
    'color',
    'id',
    'name',
    'placeholder',
    'validate'
  ],
  data () {
    return {
      colors: {
        hex: '#00BAFF'
      },
      colorValue: '',
      displayPicker: false
    }
  },
  mounted () {
    this.setColor(this.color || this.colors.hex)
  },
  updated () {
    bus.$emit('color-picker-errors', this.errors, this.name)
  },
  methods: {
    convertToString (data) {
      return data.toString(16).toUpperCase()
    },
    documentClick (e) {
      const el = this.$refs.colorpicker
      const target = e.target

      if (el !== target && !el.contains(target)) {
        this.hidePicker()
      }
    },
    setColor (color) {
      this.updateColors(color)
      this.colorValue = color
    },
    showPicker () {
      document.addEventListener('click', this.documentClick)
      this.displayPicker = true
    },
    hidePicker () {
      document.removeEventListener('click', this.documentClick)
      this.displayPicker = false
    },
    rgbaToHex ({ r, g, b, a }) {
      return ('#' + this.convertToString(r) + this.convertToString(g) + this.convertToString(b) + this.convertToString((a * 255)).substring(0, 2))
    },
    updateColors (color) {
      if (color.slice(0, 1) === '#') {
        this.colors = {
          hex: color
        }
      } else if (color.slice(0, 4) === 'rgba') {
        const rgba = color.replace(/^rgba?\(|\s+|\)$/g, '').split(',')
        const hex = '#' + ((1 << 24) + (parseInt(rgba[0]) << 16) + (parseInt(rgba[1]) << 8) + parseInt(rgba[2])).toString(16).slice(1)
        this.colors = {
          hex: hex,
          a: rgba[3]
        }
      } else {
        this.colors = {
          hex: '#00BAFF'
        }
      }
    },
    updateFromInput () {
      this.updateColors(this.colorValue)
    },
    updateFromPicker (color) {
      this.colors = color
      if (color.rgba.a === 1) {
        this.colorValue = color.hex
      } else {
        this.colorValue = this.rgbaToHex(color.rgba)
      }
    }
  },
  watch: {
    color (newVal, oldVal) {
      this.colorValue = newVal
    },
    colorValue (val) {
      if (val) {
        this.updateColors(val)
      }
    }
  }
}



