import _ from 'lodash'

export default {
  name: 'SidebarName',
  computed: {
    isDeviceChildren () {
      return _.startsWith(this.$route.path, '/device') || this.$route.path === '/'
    },
    isFirmwareChildren () {
      return _.startsWith(this.$route.path, '/firmware')
    },
    isIMChildren () {
      return _.startsWith(this.$route.path, '/informationModel')
    },
    isThemeChildren () {
      return _.startsWith(this.$route.path, '/theme')
    },
    isUserChildren () {
      return _.startsWith(this.$route.path, '/user')
    },
    showFirmwareActive () {
      return ['pageLink', { 'active': this.isFirmwareChildren }]
    },
    showIMActive () {
      return ['pageLink', { 'active': this.isIMChildren }]
    },
    showThemeActive () {
      return ['pageLink', { 'active': this.isThemeChildren }]
    }
  }
}



