import _ from 'lodash'

export default {
  TOGGLE_LOADING (state) {
    state.callingAPI = !state.callingAPI
  },
  SET_TOKEN (state, token) {
    state.token = token
  },
  SET_USER (state, user) {
    state.user = user
  },
  SET_USER_DEVICE_FOR_BROWSE (state, data) {
    state.device = _.merge(state.device, data)
  },
  SET_USER_EMAIL_FOR_DELETE (state, item) {
    state.email.id = item.id
    state.email.value = item.email
  },
  SET_USER_INFO_DATA (state, data) {
    state.userInfo = _.merge(state.userInfo, data)
  }
}



