<template>
  <section class="content">
    <button type="button" id="btn-firmware-form" @click="showAddFirmware" class="btn btn-primary">Add Firmware</button>
    <firmware-upload></firmware-upload>
    <div class="row center-block">
      <div class="box box-primary">
        <div class="box-body">
          <div class="form-inline dt-bootstrap" id="example1_wrapper">
            <div class="row">
              <div class="col-sm-12 table-responsive">
                <table role="grid" class="table">
                  <thead>
                    <tr role="row">
                      <th>Version</th>
                      <th>Previous Version</th>
                      <th>SHA1</th>
                      <th>Model</th>
                      <th>Size</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr class="even" role="row" v-for="fw in FirmwareList" :key="fw.index">
                      <td class="sorting_1">{{fw.version}}</td>
                      <td>{{fw.previousVersion}}</td>
                      <td>{{fw.sha1}}</td>
                      <td>{{fw.model}}</td>
                      <td>{{fw.size}}</td>
                      <td>
                        <a :href="fw.url" >
                          <i class="fa fa-download fa-2"></i>
                        </a>
                        <a data-toggle="modal" data-target="#delete-form" @click="delFirmware(fw)">
                          <i class="fa fa-trash fa-2"></i>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
          <!-- /.box-body -->
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import main from './Firmware.js'
export default main
</script>

<style lang="scss">
@import './Firmware.scss'
</style>



