import { bus } from '../../utils'

export default {
  computed: {
    body () {
      return this.options.text
    },
    headline () {
      return this.options.title
    }
  },
  data () {
    return {
      options: {
        buttons: [],
        text: '',
        title: ''
      },
      showModal: false
    }
  },
  methods: {
    hide (data) {
      this.showModal = false
    },
    reset () {
      this.options.buttons = []
      this.options.text = ''
      this.options.title = ''
    },
    sanitize (data) {
      this.reset()
      Object.assign(this.options, data)

      this.options.buttons.forEach(
        (button, index, array) => {
          if (!button.click) {
            button.click = this.hide.bind(this)
          }
          if (!button.class) {
            button.class = [
              'btn',
              index === array.length - 1 ? 'btn-primary' : 'btn-default'
            ].join(' ')
          }
        }
      )
    },
    show (data) {
      this.sanitize(data)
      this.showModal = true
    }
  },
  mounted () {
    bus.$on('dialog-show', this.show.bind(this))
    bus.$on('dialog-hide', this.hide.bind(this))
  }
}



