<template>
  <div ref="colorpicker">
    <input
      :class="classes || ''"
      :id="id"
      :name="name"
      :placeholder="placeholder || ''"
      v-validate="validate"
      @focus="showPicker()"
      @input="updateFromInput"
      class="input-group input-group-lg"
      type="text"
      v-model="colorValue"
    />
    <chrome-picker :value="colors" @input="updateFromPicker" v-if="displayPicker" />
  </div>
</template>

<script>
  import main from './ColorPicker.js'
export default main
</script>

<style lang="scss" scoped>
  @import './ColorPicker.scss'
</style>



