import _ from 'lodash'
import { bus } from '../../utils'

import UserInfoBox from '../UserInfoBox/UserInfoBox.vue'
import UserInfo from '../UserInfo/UserInfo.vue'

export default {
  name: 'userList',
  created () {
    return this.infiniteScrollCallback()
  },
  components: {
    UserInfoBox,
    UserInfo
  },
  computed: {
    countOfUsers () {
      return this.searchQuery
        ? `Showing ${this.searchedTotal ? 1 : 0} - ${
            this.userList.length
          } of ${this.searchedTotal}.`
        : `Showing ${this.userList.length} of ${this.total.users || 0}.`
    },
    hasResult () {
      return this.isNotFound && !_.isEmpty(this.searchQuery)
    },
    isLoadMore () {
      return this.loadCount > 3
    },
    searchNotFound () {
      return 'NO USERS FOUND'
    },
    noMoreUsers () {
      if (this.searchQuery && !_.isEmpty(this.searchQuery)) {
        return this.userList.length === this.searchedTotal
      }

      return this.userList.length === this.total.users
    }
  },
  data () {
    return {
      button: {
        loadMore: {
          onClick: this.loadMore,
          text: 'Load More'
        }
      },
      infiniteScrollDisabled: false,
      isCollapsed: {},
      isLoading: false,
      isNoMore: false,
      isNotFound: false,
      isToggle: {},
      loadCount: 0,
      offset: 0,
      subs: [],
      searchQuery: this.$route.params.email || '',
      searchedTotal: '',
      tableHeader: [
        {
          class: 'col-lg-5',
          label: 'Email'
        },
        {
          class: 'col-lg-6',
          label: 'Registered Time'
        },
        {
          class: 'col-lg-1',
          label: ''
        }
      ],
      total: {},
      userList: []
    }
  },
  methods: {
    async collapsed (index, email) {
      this.isCollapsed[email] = true
      this.isToggle[email] = !this.isToggle[email]
      this.$forceUpdate()
    },
    async getTotal () {
      let query = {
        fields: JSON.stringify([
          'users'
        ])
      }
      this.total = await this.$api
        .getTotal(query)
        .then(ret => ret.data)
        .catch(e => console.log(e))
    },
    deletePop (id, email) {
      bus.$emit('dialog-show', {
        title: 'Delete User',
        text: `
Are you sure you want to delete
<small class="label label-default">${email}</small>?
        `,
        buttons: [
          {
            text: 'Close'
          },
          {
            text: 'Delete',
            click: () => {
              bus.$emit('dialog-hide')
              this.deleteUser(id)
            }
          }
        ]
      })
    },
    deleteUser (id) {
      return this.$api.removeUser(id)
        .then(() => bus.$emit('UserDeletePopup-removeUser', id))
    },
    async getUsers (email) {
      this.isLoading = true
      let query = (email && email.trim() !== '') ? {
        filter: `{"email":"${email}"}`,
        limit: 20,
        offset: this.offset
      } : {
        limit: 20,
        offset: this.offset
      }
      return this.$api.getUsers(query)
        .then(res => res.data)
        .then(this.showList.bind(this))
        .catch(e => console.log(e))
    },
    infiniteScrollCallback () {
      this.infiniteScrollDisabled = true
      if (this.loadCount > 3) return

      this.getTotal()
      this.getUsers(this.searchQuery).then(users => {
        if (!users.items.length) return
        this.infiniteScrollDisabled = false
      })
    },
    loadMore () {
      this.loadCount = 0
      this.infiniteScrollCallback()
    },
    showList (users) {
      this.isLoading = false
      if (_.isEmpty(users.items)) {
        this.isNoMore = true
        this.searchedTotal = users.total
        return users
      }

      this.searchedTotal = users.total

      _.map(users.items, user => {
        this.isToggle[user.email] = false
        user.creation_date = user.creation_date * 1000
        return user
      })

      this.loadCount += 1
      this.userList = _.concat(this.userList, users.items)
      this.offset += 20

      return users
    },
    userSearch (email) {
      if (email.length <= 3 && email.length >= 1) return

      this.userList = []
      this.loadCount = 0
      this.offset = 0
      this.infiniteScrollDisabled = false
      this.isNoMore = false
      this.isNotFound = false

      this.getUsers(email).then(users => {
        this.isNotFound = _.isEmpty(users.items)
      })
    }
  },
  mounted () {
    bus.$on('UserDeletePopup-removeUser', id => {
      const index = this.userList.findIndex(user => user.id === id)
      this.userList.splice(index, 1)
      this.total.users -= 1
      this.isNotFound = _.isEmpty(this.userList)
    })
  }
}



