import Tab from '../Tab/Tab'
import Tabs from '../Tabs/Tabs'
import ThemeView from '../ThemeView/ThemeView'

export default {
  components: {
    Tab,
    Tabs,
    ThemeView
  },
  data () {
    return {
      fieldsData: {
        appText: {
          wifi_name: {
            class: 'form-control',
            description: 'The Wi-Fi Pattern Name is shown in the app “Connect to Your Device" screen as a reference for your end-user experience. It must be shorter than 32 characters.',
            label: 'Wi-Fi Pattern Name',
            model: null,
            validate: {
              rules: {
                max: 32
              }
            }
          }
        },
        appFile: {
          'logo_navbar.png': {
            class: 'form-control-file',
            description: 'The Navbar Logo is shown in the app at the center of the top banner and in the top of the side navigation. TIP: The file should be lightweight, with alpha transparent background, and at least 200 x 200 pixels. This image may also be on a dark background where a lighter variation will have a better performance.',
            isLocal: false,
            label: 'Navbar Logo'
          }
        },
        settingsText: {
          product_name: {
            class: 'form-control',
            description: 'The Product Name is used as a reference to your product for the end-user experience. It will be shown to users, where applicable, such as the "Add Device" process and the welcome template for new users.',
            label: 'Product Name',
            model: null,
            placeholder: 'My Product',
            validate: {
            }
          },
          primary_color: {
            class: 'form-control',
            description: 'The Primary Color is used to style the application header bar, primary buttons, device popit UI control items, and several text styles. Currently, only hexadecimal color values are supported.',
            label: 'Primary Color',
            model: null,
            placeholder: '#00BAFF',
            type: 'colorpicker',
            validate: {
              rules: {
                regex: /^#([A-Fa-f0-9]{8}|[A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/
              }
            }
          }
        },
        settingsFile: {
          'logo.png': {
            class: 'form-control-file',
            description: 'The App Logo is shown to users, where applicable, such as the "Start", "Sign In", "Create Account" screens, and the welcome template for new users. TIP: The file should be lightweight, with alpha transparent background, and 500 x 500 pixels. Remember that .png images provide best performances with text and shapes logos.',
            isLocal: false,
            label: 'App Logo'
          }
        },
        emailBasicText: {
          company_name: {
            class: 'form-control',
            description: 'Your company name to be displayed in the footer.',
            label: 'Company Name',
            model: null,
            placeholder: 'My Company',
            validate: {
            }
          },
          company_url: {
            class: 'form-control',
            description: 'The web address, which users will be directed to from the footer.',
            label: 'Company Website Url',
            model: null,
            placeholder: '#',
            validate: {
            }
          },
          company_address: {
            class: 'form-control',
            description: 'Your company address to be displayed in the footer.',
            label: 'Company Address',
            model: null,
            placeholder: '275 Market St, Suite 535, Minneapolis, MN 55405',
            validate: {
            }
          },
          company_contact: {
            class: 'form-control',
            description: 'Your company contact information to be displayed in the footer.',
            label: 'Company Contact',
            model: null,
            placeholder: '1-612-353-2161',
            validate: {
            }
          },
          apple_store: {
            class: 'form-control',
            description: 'The web address of your app in the Apple App Store, which users will be directed to from email and web pages.',
            label: 'Apple Store Url',
            model: null,
            placeholder: 'https://itunes.apple.com/us/app/exosite-excite/id1090866808',
            validate: {
            }
          },
          google_play: {
            class: 'form-control',
            description: 'The web address of your app in the Google Play Store, which users will be directed to from email and web pages.',
            label: 'Google Play Url',
            model: null,
            placeholder: 'https://play.google.com/store/apps/details?id=com.exosite.excite',
            validate: {
            }
          },
          app_name: {
            class: 'form-control',
            description: 'Your app name to be displayed in email and web pages to direct users to open your app.',
            label: 'App Name',
            model: null,
            placeholder: 'myProduct',
            validate: {
            }
          },
          app_url_scheme: {
            class: 'form-control',
            description: 'The URL scheme of your app, which allows users to be redirected to your app from email and web pages. This should match the “ionic-plugin-deeplinks” setup in your app’s “config.xml” and “package.json” files.',
            label: 'App Url Scheme',
            model: null,
            placeholder: 'myproduct://',
            validate: {
            }
          }
        },
        emailTemplateText: {
          welcome_email_subject: {
            class: 'form-control',
            label: 'Welcome Email Subject',
            model: null,
            placeholder: 'Welcome to {{product_name}}!',
            validate: {
            }
          },
          welcome_email_headline: {
            class: 'form-control',
            label: 'Welcome Email Headline',
            model: null,
            placeholder: 'Welcome to {{product_name}}!',
            validate: {
            }
          },
          welcome_email_body: {
            class: 'form-control',
            label: 'Welcome Email Body',
            model: null,
            placeholder: 'Thanks for creating a {{product_name}} account. With the {{app_name}} app, you can control your smart AC from anywhere and set schedules to fit your daily routine.',
            validate: {
            }
          },
          welcome_web_headline: {
            class: 'form-control',
            label: 'Welcome Web Headline',
            model: null,
            placeholder: 'Welcome to {{product_name}}!',
            validate: {
            }
          },
          welcome_web_body: {
            class: 'form-control',
            label: 'Welcome Web Body',
            model: null,
            placeholder: 'With the {{app_name}} app, you can control your smart AC from anywhere and set schedules to fit your daily routine. If you haven’t yet, make sure to download the {{app_name}} app on any device you wish to use.',
            validate: {
            }
          }
        },
        legalPoliciesText: {
          title: {
            class: 'form-control',
            description: 'The headline to be displayed in the legal web page.',
            label: 'Web Headline',
            model: null,
            placeholder: 'Terms & Conditions and Privacy Policy',
            type: 'text',
            validate: {
            }
          },
          body: {
            class: 'form-control',
            description: 'The legal content to be displayed in the legal web page.',
            label: 'Web Body',
            model: null,
            type: 'textarea',
            validate: {
            }
          }
        }
      },
      muranoTermsUrl: `https://exosite.com/murano-terms-conditions/`,
      legalUrl: `${window.location.origin}/#/legal`
    }
  },
  methods: {
    async getTheme () {
      // TODO: Make sure there has no conflict keys when do the Object.assign()
      const config = await Promise.all([
        this.$api.getTheme(),
        this.$api.getLegal()
      ]).then(([themeData, legalData]) => Object.assign({}, themeData.data, legalData.data))

      Object.keys(this.$data.fieldsData).forEach((blockKey) => {
        const obj = this.$data.fieldsData[blockKey]
        for (let key in obj) {
          const input = obj[key]
          const value = config[key]

          if (value) input.model = value
        }
      })

      return config
    }
  },
  mounted () {
    return this.getTheme()
  }
}



