<template>
  <div class="modal" id="firmware-form" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" v-show="showModal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" @click="close" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
          <h3 class="modal-title" id="exampleModalLabel">Firmware Upload</h3>
        </div>
        <form class="form-horizontal" id="uploadform" @submit.prevent="uploadFirmware" enctype="multipart/form-data">
          <div class="modal-body row">
            <div class="form-group" :class="{ 'control': true }">
              <label class="col-xs-3 col-md-3 control-label" for="New Version">New Version</label>
              <div class="input-group col">
                <input v-model="data.version" v-validate="{ rules: { regex: validate.semver} }" :class="{'input': true, 'is-danger': errors.has('version') }" name="version" class="form-control" placeholder="Version" type="text" required>
                <div class="feedback help is-danger" v-show="errors.has('version')">Must be semver (X.X.X)</div>
              </div>
            </div>
            <div class="form-group" :class="{ 'control': true }">
              <label class="col-xs-3 col-md-3 control-label" for="Previous Version">Previous Version</label>
              <div class="input-group col">
                <input v-model="data.lastVersion" v-validate="{ rules: { regex: validate.semver} }" :class="{'input': true, 'is-danger': errors.has('previous') }" name="previous" class="form-control" placeholder="Previous Version" type="text" required>
                <div class="feedback help is-danger" v-show="errors.has('previous')">Must be semver (X.X.X)</div>
              </div>
            </div>
            <div class="form-group" :class="{ 'control': true }">
              <label class="col-xs-3 col-md-3 control-label" for="SHA1">SHA1</label>
              <div class="input-group col">
                <input v-model="data.sha1" v-validate="{ rules: { regex: validate.sha1} }" :class="{'input': true, 'is-danger': errors.has('sha1') }" name="sha1" class="form-control" placeholder="SHA1" maxlength="40" type="text" required>
                <div class="feedback help is-danger" v-show="errors.has('sha1')">Must be sha1 (40 hex characters)</div>
              </div>
            </div>
            <div class="form-group" :class="{ 'control': true }">
              <label class="col-xs-3 col-md-3 control-label" for="Model">Model</label>
              <div class="input-group col">
                <input v-model="data.model" v-validate="{ rules: { regex: validate.model} }" :class="{'input': true, 'is-danger': errors.has('model') }" name="model" class="form-control" placeholder="Model" type="text" required>
                <div class="feedback help is-danger" v-show="errors.has('model')">Model must be between 2-32 characters.</div>
              </div>
            </div>
            <div class="form-group" :class="{ 'control': true }">
              <label class="col-xs-3 col-md-3 control-label" for="Firmware Image">Firmware Image</label>
              <div class="input-group col">
                <input v-bind="data.imgFile" id="file" v-validate="'required'" placeholder="No file chosen" required type="file" :name="uploadFieldName" @change="filesChange($event.target.name, $event.target.files); fileCount = $event.target.files.length" class="input-file form-control">
                <span class="custom-file-control"></span>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <div class="progress">
              <div class="progress-bar progress-bar-striped active" v-bind:style="{ width: percentCompleted}">
              </div>
            </div>
            <button type="button" class="btn btn-secondary" @click="close">Close</button>
            <button type="submit" class="btn btn-primary">Upload</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import main from './FirmwareUpload.js'
export default main
</script>

<style lang="scss">
@import './FirmwareUpload.scss'
</style>



