<template>
  <div class="box-body">
    <time-line
        :is-clicked="isClicked"
        :target="user.email"
    ></time-line>
    <div class="col-sm-12">
      <div class="box box-gray">

        <div class="box-header with-border">
          <h3 class="box-title">ALEXA INTEGRATION</h3>
        </div>
        <div class="box-body">
          <dl class="dl-horizontal">
            <div class="col-sm-12">
              <dt>STATUS</dt>
              <dd>
                {{alexaStatus}}
              </dd>
            </div>
          </dl>
        </div>
      </div>
    </div>
    <div class="col-sm-12">
      <div class="box box-gray">
        <div class="box-header with-border">
          <h3 class="box-title">Devices</h3>
        </div>
        <div class="box-body">
          <dl class="dl-horizontal">
            <div class="col-sm-12">
              <dt>SN</dt>
              <dd v-for="device in devices" :key="device.id">
                <a :href="'#/device/' + device.id">
                  {{device.id}}
                </a>
              </dd>
              <dt>#</dt>
              <dd>{{devices.length | pluralize('Device')}}</dd>
            </div>
          </dl>
        </div>
      </div>
    </div>
    <div class="col-sm-12">
      <div class="box box-gray">
        <div class="box-header with-border">
          <h3 class="box-title">My Groups</h3>
        </div>
        <div class="box-body">
          <dl class="dl-horizontal">
            <div class="col-sm-12">
              <dt>Name</dt>
              <dd v-for="(group, index) in groups" :key="index">{{group.name}}</dd>
              <dt>#</dt>
              <dd>{{groups.length | pluralize('Group')}}</dd>
            </div>
          </dl>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import main from './UserInfo.js'
export default main
</script>
<style lang="scss" scoped>
@import './UserInfo.scss'
</style>



