<template>
  <section class="content"
    infinite-scroll-disabled="infiniteScrollDisabled"
    infinite-scroll-distance="20"
    infinite-scroll-immediate-check="false"
    v-infinite-scroll="infiniteScrollCallback"
  >
    <user-infobox :total="total"></user-infobox>
    <div class="row center-block">
      <div class="col-sm-3 col-sm-offset-9">
        <div class="input-group search">
          <span class="input-group-addon">
            <i class="fa fa-search"></i>
          </span>
          <input
            @change="userSearch(searchQuery)"
            class="form-control
            input-sm"
            placeholder="Search Email"
            type="search"
            v-model.lazy="searchQuery"
          >
        </div>
      </div>
      <div class="col-md-12">
        <div class="box table-list">
          <div class="box-body">
            <div class="form-inline">
              <div class="row">
                <div class="col-sm-12">
                  <div class="panel box title">
                    <div class="box-header">
                      <div :class="item.class" v-for="(item, index) in tableHeader" :key="index">
                        <strong>{{item.label}}</strong>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="col-sm-12">
                  <div id="accordion" class="panel box box-header-row" v-for="(item, index) in userList" :key="item.id">
                    <div :class="['box-header', { clicked: isToggle[item.email] } ]" @click="collapsed(index, item.email)">
                      <div class="collapsed" data-toggle="collapse" data-parent="#accordion" aria-expanded="false" aria-controls="collapseTwo">
                        <div class="col-lg-5 box-title">{{item.email}}</div>
                        <div class="col-lg-6 box-title">{{item.creation_date | formatDate}}</div>
                        <div class="col-lg-1 box-title">
                          <a @click.stop="deletePop(item.id, item.email)">
                            <i class="fa fa-trash fa-2"></i>
                          </a>
                        </div>
                      </div>
                    </div>
                    <transition :id="index" role="tabpanel" aria-labelledby="headingTwo">
                      <user-info class="panel-collapse" v-show="isToggle[item.email]" :user="item" :is-clicked="isCollapsed[item.email]"></user-info>
                    </transition>
                  </div>
                  <div id="loading-icon" v-show="isLoading">
                    <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
                    <span class="sr-only">Loading...</span>
                  </div>
                  <div class="col-sm-12 text-center" v-show="hasResult">
                    <h4>{{searchNotFound}}</h4>
                  </div>
                  <div class="col-sm-12 footer" v-show="userList && !isLoading && !hasResult">
                    <div class="col-lg-12 load-more" v-show="isLoadMore && !noMoreUsers">
                      <button class="btn btn-block btn-primary" @click="button.loadMore.onClick()">{{button.loadMore.text}}</button>
                    </div>
                    <div>
                      {{countOfUsers}}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
  import main from './User.js'
  export default main
</script>
<style lang="scss" scoped>
  @import './User.scss';
</style>



