// Import System requirements
import Vue from 'vue'
import VeeValidate from 'vee-validate'
import VideoBg from 'vue-videobg'
import Vue2Leaflet from 'vue2-leaflet'
import VueCookie from 'vue-cookie'
import VueRouter from 'vue-router'
import VTooltip from 'v-tooltip'
import infiniteScroll from 'vue-infinite-scroll'

import { sync } from 'vuex-router-sync'
import routes from './routes'
import store from './store'
import { durations, formatDate, isConnective, isProvision, pluralize } from './filters'
import {
  App as AppView,
  ColorPicker,
  DeviceInfoBox,
  DeviceListItem,
  Dialog,
  FirmwareUpload,
  InformationModelUpload,
  Tab,
  Tabs,
  ThemeView,
  TimelineLog,
  TimelineModal,
  UserInfo,
  UserInfoBox
} from './components'
import config from './config'
import API from './api'

Vue.component('colorpicker', ColorPicker)
Vue.component('video-bg', VideoBg)
Vue.component('v-map', Vue2Leaflet.Map)
Vue.component('v-marker', Vue2Leaflet.Marker)
Vue.component('v-tilelayer', Vue2Leaflet.TileLayer)
Vue.component('device-infobox', DeviceInfoBox)
Vue.component('device-listitem', DeviceListItem)
Vue.component('hamv-dialog', Dialog)
Vue.component('firmware-upload', FirmwareUpload)
Vue.component('im-upload', InformationModelUpload)
Vue.component('tab', Tab)
Vue.component('tabs', Tabs)
Vue.component('theme-view', ThemeView)
Vue.component('timeline-log', TimelineLog)
Vue.component('timeline-modal', TimelineModal)
Vue.component('user-info', UserInfo)
Vue.component('user-infobox', UserInfoBox)

Vue.use(VTooltip)
Vue.use(VeeValidate, { fieldsBagName: 'formFields' })
Vue.use(VueCookie)
Vue.use(VueRouter)
Vue.use(infiniteScroll)
Vue.use(API)

var router = new VueRouter({
  routes: routes,
  // mode: 'history',
  linkExactActiveClass: 'active',
  scrollBehavior: function (to, from, savedPosition) {
    return savedPosition || { x: 0, y: 0 }
  }
})
Vue.filter('durations', durations)
Vue.filter('formatDate', formatDate)
Vue.filter('isConnective', isConnective)
Vue.filter('isProvision', isProvision)
Vue.filter('pluralize', pluralize)

router.beforeEach((to, from, next) => {
  let storeToken = router.app.$store.state.token
  storeToken = null
  if (router.app.$cookie.get('sid')) {
    storeToken = router.app.$cookie.get('sid')
  }

  const localStorageToken = window.localStorage.getItem('token')
  const noLoginPaths = config.noLoginPaths

  if ((noLoginPaths.indexOf(to.path) === -1) && (!storeToken || !localStorageToken)) {
    // logout
    window.console.log('Not authenticated')
    store.commit('SET_TOKEN', null)
    store.commit('SET_USER', '')
    router.app.$cookie.delete('sid')
    window.localStorage.clear()

    next({ path: '/login' })
  } else if ((to.path === '/login/' || to.path === '/login') && storeToken) {
    next({ path: '/device' })
  } else {
    next()
  }
})

sync(store, router)

// eslint-disable-next-line no-new
new Vue({
  el: '#root',
  router: router,
  store: store,
  render: h => h(AppView)
})

if (window.localStorage) {
  const localUserToken = window.localStorage.getItem('token') || null
  const username = window.localStorage.getItem('user') || null

  if (localUserToken && store.state.token !== localUserToken) {
    store.commit('SET_TOKEN', window.localStorage.getItem('token'))
  }

  if (username) store.commit('SET_USER', username)
}



