import Ajv from 'ajv'
import { bus, form } from '../../utils'
import imSchema from '../../utils/json_schema.json'
import yaml from 'js-yaml'

const ajv = new Ajv({
  allErrors: true
})
ajv.addSchema(imSchema, 'im')

export default {
  name: 'AddInformationModelPopup',
  data () {
    return {
      alert$: {
        show: false
      },
      buttons: [
        {
          class: 'btn btn-secondary',
          onClick: this.close,
          text: 'Close',
          type: 'button'
        },
        {
          class: 'btn btn-primary',
          onClick: () => {},
          text: 'Save',
          type: 'submit'
        }
      ],
      file: {
        infoModels: {
          class: 'form-control-file',
          label: 'Upload'
        }
      },
      loading: false,
      showModal: false
    }
  },
  methods: {
    alert (style, message) {
      this.alert$.class = 'alert-' + style
      this.alert$.text = message
      this.alert$.show = true
    },
    async updateInformationModels (e) {
      if (this.loading) return
      this.loading = true
      this.alert('info', 'Uploading...')

      return Promise.all(
        this.filterFiles(form.getFormData(e).getAll('infoModels'))
      )
        .then(this.validateFiles.bind(this))
        .then(data => this.$api.updateInfoModel(data))
        .then(r => r.data)
        .then(models => bus.$emit('updateModelList', models))
        .then(this.successCallback.bind(this))
        .catch(this.failedCallback.bind(this))
    },
    close () {
      this.reset()
      this.alert$.show = false
      this.showModal = false
    },
    failedCallback (e) {
      let errorMessage = e.message || e.body
      this.loading = false
      this.alert('danger', errorMessage)
    },
    filterFiles (files) {
      return files
        .filter(
          file =>
            file.type === 'application/json' ||
            file.type === 'application/x-yaml' ||
            file.type === ''
        )
        .map(this.readFiles.bind(this))
    },
    readFiles (file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()

        reader.onload = () => {
          resolve({
            lastModified: file.lastModified,
            name: file.name,
            size: file.size,
            text: reader.result
          })
        }

        reader.readAsText(file)
      })
    },
    reset () {
      document.getElementById('infoModels').value = ''
    },
    submit (e) {
      switch (e.type) {
        case 'submit':
          this.updateInformationModels(e)
          break
      }
    },
    successCallback () {
      this.loading = false
      this.alert('success', 'Success')
      this.close()
    },
    validateFiles (array) {
      let errorMessage = ''
      let allModel = array.reduce((acc, val) => {
        let model = {}

        try {
          model = JSON.parse(val.text)
        } catch (e) {
          model = yaml.safeLoad(val.text)
        }

        const valid = ajv.validate('im', model)
        if (valid) {
          acc[model.familyName] = val
        } else {
          ajv.errors.forEach(
            e => {
              errorMessage += `${val.name}: ${e.dataPath} ${e.message}.\n`
            }
          )
        }

        return acc
      }, {})

      if (errorMessage) {
        throw new Error(errorMessage)
      }

      return allModel
    }
  },
  mounted () {
    bus.$on('add-information-modal', (show) => {
      if (show) {
        this.showModal = true
      }
    })
  }
}



