import _ from 'lodash'
import { bus } from '../../utils'

export default {
  computed: {
    filteredList () {
      return Array.isArray(this.lists) ? this.lists.filter(
        log => {
          switch (log.type) {
            case 'ERROR':
              return this.headerCheckFilter[0].model
            case 'EVENT':
              return this.headerCheckFilter[1].model
            default:
              console.log(log.type)
          }
        }
      ) : this.lists
    },
    isDevicesPage () {
      return _.startsWith(this.$route.path, '/device')
    }
  },
  data () {
    return {
      connected: null,
      headerCheckFilter: [
        {
          text: 'Errors',
          model: true
        },
        {
          text: 'Events',
          model: true
        }
      ],
      lists: null,
      lastLogTime: undefined,
      infiniteScrollDisabled: false,
      isLoading: false,
      isNoMore: false,
      showModal: false
    }
  },
  methods: {
    infiniteScrollCallback () {
      this.infiniteScrollDisabled = true
      this.getLog()
        .then(
          (logs) => {
            if (!logs.length) return
            this.infiniteScrollDisabled = false
          }
        )
    },
    close () {
      bus.$emit('TimelineModal-hide')
    },
    getLog () {
      this.isLoading = true
      return this.$api.getLogs({
        end_time: this.lastLogTime,
        limit: 20,
        tags: this.tagsFormat(this.target)
      })
        .then(res => res.data)
        .then(this.showList.bind(this))
        .catch(e => console.log(e))
    },
    viewLogInfo (status) {
      let data = {
        content: ''
      }
      _.forEach(
        status,
        (value, key) =>
          (data.content += `<div class="col-md-4 text-left">${key}: ${value}</div>`)
      )
      return data
    },
    tagsFormat (target) {
      let format = _.includes(target, '@')
        ? { email: target, log_type: 'event' }
        : { device_unique_id: target }
      return JSON.stringify(format)
    },
    reset () {
      this.isLoading = false
      this.isNoMore = false
      this.lastLogTime = undefined
      this.lists = null
    },
    showList (logs) {
      this.isLoading = false
      if (_.isEmpty(logs)) {
        this.isNoMore = true
      }

      if (!this.lists) {
        this.lists = []
      }

      _.forEach(logs, (item, index) => {
        this.lists.push({
          action: item.action,
          from: item.from,
          ip: item.ip,
          type: _.toUpper(item.type),
          status: this.viewLogInfo(item.status),
          time: item.time
        })
        this.lastLogTime = _.has(item, 'time')
          ? new Date(item.time).toJSON()
          : ''
      })
      return logs
    }
  },
  mounted () {
    bus.$on('TimelineModal-show', data => {
      this.showModal = true
      this.reset()
      Object.assign(this, _.pick(data, [
        'connected',
        'target'
      ]))
      this.$emit('invokeInfiniteScroll')
    })
    bus.$on('TimelineModal-hide', data => {
      this.showModal = false
    })
    if (!this.showModal) {
      this.infiniteScrollDisabled = false
    }
  }
}



