import _ from 'lodash'
import { bus } from '../../utils'
import DeviceListItem from '../DeviceListItem/DeviceListItem.vue'
import DeviceInfoBox from '../DeviceInfoBox/DeviceInfoBox.vue'

export default {
  name: 'DeviceList',
  async created () {
    this.getTotal()
    if (this.$route.params.sn) {
      return this.deviceSearch(this.$route.params.sn)
    }

    this.infiniteScrollCallback()
  },
  computed: {
    hasResult () {
      return this.isNotFound && !_.isEmpty(this.searchQuery)
    },
    isLoadMore () {
      return this.loadCount > 3
    },
    searchNotFound () {
      return 'NO DEVICES FOUND'
    }
  },
  components: {
    DeviceListItem,
    DeviceInfoBox
  },
  mounted () {
    bus.$on('DeviceLockPopup-lockDevice', (id) => {
      const device = this.deviceList.find((item) => item.identity === id)
      device.locked = true
    })
  },
  data () {
    return {
      button: {
        loadMore: {
          onClick: this.loadMore,
          text: 'Load More'
        }
      },
      infiniteScrollDisabled: false,
      isLoading: true,
      isNoMore: false,
      isNotFound: false,
      loadCount: 0,
      offset: 0,
      needSearchTips: 'If you cannot find the device you are searching for in the list, please utilize the search function above.',
      searchQuery: this.$route.params.sn || '',
      subs: [],
      tableHeader: [
        {
          class: 'col-sm-5',
          label: 'SN'
        },
        {
          class: 'col-sm-2',
          label: 'Model'
        },
        {
          class: 'col-sm-2',
          label: 'Connectivity Status'
        },
        {
          class: 'col-sm-2',
          label: 'Firmware Ver.'
        },
        {
          class: 'col-sm-1',
          label: ''
        }
      ],
      total: {},
      deviceList: []
    }
  },
  methods: {
    async getTotal () {
      let query = {
        fields: JSON.stringify([
          'connected_devices',
          'devices'
        ])
      }
      this.total = await this.$api.getTotal(query)
      .then(ret => ret.data)
      .catch(e => console.log(e))
    },
    async getDevices (sn = '') {
      this.isLoading = true

      let query = sn
        ? {
          identity: sn,
          limit: 50,
          offset: this.offset
        }
        : {
          limit: 50,
          offset: this.offset
        }
      return this.$api.getDevices(query)
        .then(res => res.data)
        .then(this.showList.bind(this))
        .catch(e => console.log(e))
    },
    deviceSearch (sn) {
      this.deviceList = []
      this.loadCount = 0
      this.offset = 0
      this.isNotFound = false
      this.infiniteScrollDisabled = true

      this.getDevices(sn)
      .then((devices) => {
        this.isNotFound = _.isEmpty(devices)
      })
    },
    infiniteScrollCallback () {
      this.infiniteScrollDisabled = true

      if (this.loadCount > 3) return

      this.getDevices(this.searchQuery)
        .then(
          (devices) => {
            if (_.isEmpty(devices)) return
            this.infiniteScrollDisabled = true
          }
        )
    },
    loadMore () {
      this.loadCount = 0
      this.infiniteScrollCallback()
    },
    showList ({ devices }) {
      this.isLoading = false
      if (_.isEmpty(devices)) {
        this.isNoMore = true
        return devices
      }
      this.loadCount += 1
      this.deviceList = _.concat(this.deviceList, devices)
      this.offset += 20

      return devices
    }
  }
}



