<template>
  <div class="modal" tabindex="-1" role="dialog" v-show="showModal">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" @click="close" aria-label="Close">
            <i class="icon ion-ios-close-outline fa-lg"></i>
          </button>
          <div class="col-md-5 modal-title">Timeline</div>
          <div class="col-md-6 checkbox header-filter" v-show="isDevicesPage">
            <label v-for="item in headerCheckFilter" :key="item.text">
              <input type="checkbox" v-model="item.model">
              <span>{{item.text}}</span>
            </label>
          </div>
        </div>
        <div class="modal-body"
          infinite-scroll-disabled="infiniteScrollDisabled"
          infinite-scroll-distance="20"
          infinite-scroll-immediate-check="false"
          infinite-scroll-listen-for-event="invokeInfiniteScroll"
          v-infinite-scroll="infiniteScrollCallback"
        >
          <timeline-log :connected="connected" :log-list="filteredList"></timeline-log>
          <div id="loading-icon" v-if="isLoading">
            <i class="fa fa-spinner fa-pulse fa-2x fa-fw"></i>
            <span class="sr-only">Loading...</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import main from './TimelineModal.js'
export default main
</script>

<style lang="scss" scoped>
@import './TimelineModal.scss'
</style>



